import React, { useRef } from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { FaCopy } from 'react-icons/fa'

import styles from './Requisits.module.scss'

const Field = ({ title, name, text }) => {
    const input = useRef(null)
    return (
        <label className={styles.field}>
            <span className={styles.field__title} title={title}>{name}</span>
            <span className={styles.field__textfield}>
                <input className={styles.field__text} type="text" value={text} onChange={e => e.preventDefault()} ref={input} />
                <span className={styles.field__control}>
                    <CopyToClipboard onCopy={() => input.current.focus()} text={text}>
                        <FaCopy />
                    </CopyToClipboard>
                </span>
            </span>
        </label>
    )
}

export default Field
